function unix(dt,type){
  const seconds = dt * 1000
  const data = new Date(seconds)
  let result = 
  type == 'day' ? data.toLocaleDateString('ru-RU',{ day:'numeric'}) : 
  type == 'month' ? data.toLocaleDateString('ru-RU',{ month:'long'}):
  type == 'weekday' ? data.toLocaleDateString('ru-RU',{ weekday:'long'}): ''

  return result


}

export default unix